/**
 * The following styles get applied inside the editor only.
 */

.wp-block-fiigbl-editors-note {
	min-height: 200px;
	box-sizing: border-box;
  padding: 1em;
  margin: 0;
  color: #1e1e1e;
  border-radius: 2px;
  background-color: #fff;
  box-shadow: inset 0 0 0 1px #1e1e1e;
  outline: 1px solid transparent;
}
